d: none;\n    padding: 0;\n    cursor: pointer;\n    outline: none;\n    backface-visibility: hidden;\n    overflow: hidden; // Makes the rotated i box not clickable.\n    user-select: none;\n    opacity: 0;\n    transition: opacity $hover-transition-duration ease-in-out;\n    pointer-events: none;\n\n    i {\n        display: block;\n        transition: transform $add-transition-duration $bounce-transition-timing;\n        font-style: normal;\n        font-size: $add-button-font-size;\n        line-height: $add-button-size;\n    }\n\n    &--visible {\n        opacity: 1;\n        pointer-events: unset;\n    }\n\n    &--closed i {\n        transform: rotate(45deg);\n    }\n}\n","$grid-gutter-width: 30px !default;\n$header-padding-horizontal: 8px !default;\n$header-padding-vertical: 8px !default;\n$block-margin-vertical: 4px !default;\n$block-margin-horizontal: 0 !default;\n$add-button-size: 34px !default;\n$add-button-font-size: 28px !default;\n$type-button-padding-vertical: 6px !default;\n$type-button-padding-horizontal: 8px !default;\n$children-container-padding: $add-button-size / 2 !default;\n$action-font-size: 15px !default;\n$add-transition-duration: 0.3s !default;\n$hover-transition-duration: 0.3s !default;\n$bounce-transition-timing: cubic-bezier(0.175, 0.885, 0.32, 1.275) !default;\n$border-radius: 3px !default;\n$teal: #007d7e !default;\n$header-text-color-focus: #4d4d4d !default;\n$header-text-color: #888888 !default;\n$block-border-color: #e6e6e6 !default;\n$block-border-color-focus: #bbbbbb !default;\n$block-hover-background: #f5f5f5 !default;\n$error-color: #cd3238 !default;\n$error-border-color: #dbc7c8 !default;\n$error-border-color-focus: #cdb2b3 !default;\n$error-background-color: #fbefef !default;\n$screen-xs-max: 799px !default;\n$screen-sm-min: 800px !default;\n",".c-sf-add-panel {\n    position: relative;\n    padding: $grid-gutter-width / 4;\n    background: #EEEEEE;\n    border-radius: $border-radius;\n    user-select: none;\n    &__group-title {\n        margin: $grid-gutter-width / 4 $grid-gutter-width / 4 0;\n    }\n}\n",".c-sf-button {\n    margin: $grid-gutter-width / 4;\n    appearance: none;\n    background: $teal;\n    padding: 0;\n    border: 0 none;\n    border-radius: $border-radius;\n    outline: none;\n    cursor: pointer;\n    overflow: hidden;\n\n    &__icon,\n    &__label {\n        display: inline-block;\n\n        // Fixes for label elements getting global `label` styles from Wagtail -@jonnyscholes\n        color: white;\n        font-weight: normal;\n    }\n\n    &__icon {\n        padding: $type-button-padding-vertical 0\n                 $type-button-padding-vertical $type-button-padding-horizontal;\n    }\n\n    &__label {\n        padding: $type-button-padding-vertical $type-button-padding-horizontal;\n        text-transform: uppercase;\n    }\n}\n","// TODO: Reduce nesting further by splitting out more components.\n//\n// Theres quite a lot of nesting in here which makes parsing some segments difficult.\n// Some of this is to deal with the fact that .c-sf-block can contain many .c-sf-block's and so is\n// legitimate. A lot of these would ideally be their own components (eg the actions) however there\n// is a lot of interdependancy of the elements because of COLLAPSIBLE vs SIMPLE vs default which\n// makes this hard without fairly intensive rethinking of the HTML.\n//\n// However, the new classes adequately sanitise streamfield only CSS so am leaving this for\n// now to avoid blocking the release of the new Streamfield. -@jonnyscholes\n\n// TODO: Consider visually moving .c-sf-block__type and .c-sf-block__actions based on COLLAPSIBLE vs\n// SIMPLE vs default using CSS only instead of moving HTML around. -@jonnyscholes\n\n.c-sf-block {\n    flex: 1 1 auto;\n    margin: $block-margin-vertical $block-margin-horizontal;\n    border: 1px solid $block-border-color;\n    border-radius: $border-radius;\n    background: white;\n    transition: border-color $hover-transition-duration ease-in-out;\n    transition-property: border-color, box-shadow;\n\n    &__header {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        padding-left: $header-padding-horizontal;\n        user-select: none;\n        transition: background-color $hover-transition-duration ease-in-out;\n        cursor: default;\n\n        &--collapsible {\n            cursor: pointer;\n        }\n\n        &--sortable {\n            cursor: grab;\n        }\n\n        &__title {\n            display: inline-block;\n            flex: 1 1 auto;\n            margin: 0;\n            color: $header-text-color;\n            vertical-align: middle;\n\n            &__icon {\n                color: currentColor;\n                transition: color $hover-transition-duration ease-in-out;\n            }\n        }\n    }\n\n    &__content {\n        &-inner {\n            padding: 12px;\n        }\n    }\n\n    &__actions {\n        white-space: nowrap;\n\n        &__single {\n            appearance: none;\n            border: 0 none;\n            padding: $header-padding-vertical $header-padding-horizontal;\n            background: none;\n            cursor: pointer;\n            color: $header-text-color;\n            opacity: 1;\n            transition: opacity $hover-transition-duration ease-in-out;\n            transition-property: opacity, color;\n            font-size: $action-font-size;\n\n\n            i {\n                font-style: normal;\n                &:before {\n                    margin: 0;\n                }\n            }\n\n            &[disabled] {\n                opacity: 0.2;\n                pointer-events: none;\n            }\n        }\n    }\n\n    &__type {\n        margin-right: $header-padding-horizontal * 2;\n        text-transform: uppercase;\n        text-align: right;\n        font-size: 80%;\n        line-height: 100%;\n        color: $header-text-color;\n        vertical-align: middle;\n        user-select: none;\n    }\n\n    &.c-sf-block--error {\n        border-color: $error-border-color;\n\n        > .c-sf-block__header {\n            background: $error-background-color;\n        }\n\n        &:hover,\n        &:focus,\n        &:focus-within {\n            border-color: $error-border-color-focus;\n\n            > .c-sf-block__header {\n                background: $error-background-color;\n            }\n        }\n    }\n\n    &.SIMPLE {\n        display: flex;\n        flex-flow: row nowrap;\n\n        > .c-sf-block__header {\n            position: relative;\n            flex: 0 0 auto;\n            padding: $header-padding-vertical $header-padding-horizontal;\n\n            .c-sf-block__header__title {\n                position: sticky;\n                top: $grid-gutter-width / 4;\n                bottom: $grid-gutter-width / 4;\n            }\n        }\n\n        > .c-sf-block__content {\n            flex: 1 1 auto;\n            display: flex;\n            flex-flow: column nowrap;\n            justify-content: space-around;\n            width: 0; // Trick to prevent content from overflowing, while\n            // allowing for some overflow, like Draftail tooltips.\n        }\n\n        > .c-sf-block__aside {\n            display: flex;\n            flex-flow: column nowrap;\n            align-items: flex-end;\n\n            .c-sf-block__type {\n                margin: 0 $header-padding-horizontal\n                        $header-padding-vertical $header-padding-horizontal;\n            }\n        }\n    }\n\n    &.COLLAPSIBLE {\n        overflow: hidden;\n\n        > .c-sf-block__header {\n            .c-sf-block__header__title {\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n            }\n\n            .c-sf-block__header__title__icon {\n                margin-right: $header-padding-horizontal;\n            }\n\n            > .c-sf-block__aside {\n                flex: 0 0 auto;\n            }\n        }\n    }\n\n    &:hover,\n    &:focus,\n    &:focus-within {\n        border-color: $block-border-color-focus;\n        box-shadow: 3px 2px 3px -1px rgba(0, 0, 0, 0.1);\n\n        > .c-sf-block__header {\n            background: $block-hover-background;\n\n            .c-sf-block__header__title,\n            .c-sf-block__actions__single {\n                color: $header-text-color-focus;\n            }\n        }\n    }\n}\n",".c-sf-container {\n    position: relative;\n    display: flex;\n    flex-flow: column nowrap;\n    padding: $children-container-padding 0;\n    transition: padding $hover-transition-duration ease-in-out;\n\n    &__block-container {\n        position: relative;\n        display: flex;\n        flex-flow: column nowrap;\n    }\n\n    &--add-in-gutter {\n        @media (min-width: $screen-sm-min) {\n            padding-left: $add-button-size;\n\n            .c-sf-add-button {\n                width: $add-button-size;\n                height: 0;\n                transform: translate(-100%, -$add-button-size / 2);\n                overflow: visible;\n            }\n        }\n    }\n\n    // TODO: Remove these references to classes that are styled by core Wagtail CSS. The best\n    // opportunity for this is propably as part of Wagtails general CSS overhaul. -@jonnyscholes\n    .field {\n        + .field {\n            padding-top: $grid-gutter-width / 2;\n        }\n\n        &__label {\n            display: block;\n            font-weight: bold;\n            margin-bottom: $grid-gutter-width / 4;\n        }\n\n        &.required > label::after {\n            content: '*';\n            color: #cd3238;\n            font-weight: 700;\n            display: inline-block;\n            margin-left: 0.5em;\n            line-height: 1em;\n            font-size: 13px;\n        }\n    }\n}\n"],"sourceRoot":""}]);

